export function updatePostNetworkData(network){
  return {
    network_device_name: network.network_device_name,
    network_device_object_id: network.network_device_object_id || 0,
    network_enable: network.network_enable || 0,
    network_ip: network.network_ip,
    network_mask: network.network_mask || 0,
    network_name: network.network_name,
    network_port: network.network_port || 0,
  }
};

export function updatePostDeviceData(device){
  return {
    device_enable: device.device_enable || 0,
    device_ip: device.device_ip,
    device_mac: device.device_mac || 0,
    device_mask: device.device_mask || 0,
    device_name: device.device_name,
    device_object_id: device.device_object_id || 0,
    device_port: device.device_port || 0,
    network_number: device.network_number || 0,
    network_uuid: device.network_uuid,
    supports_rpm: device.supports_rpm || false,
    supports_wpm: device.supports_wpm || false,
    type_mstp: device.type_mstp || false,
  }
};

export function updatePostPointData(point){
  return {
    device_uuid: point.device_uuid,
    point_enable: point.point_enable || false,
    point_name: point.point_name,
    point_object_id: point.point_object_id || 0,
    point_object_type: point.point_object_type,
  }
};